import React, { useState, useEffect, useCallback, useRef, useMemo } from 'react';
import { useParams } from 'react-router-dom';
import Swal from 'sweetalert2';
import { useSocket } from '../../useSocket';
import { getDistance } from 'geolib';

import { joinApi } from '../../apiBase';
import Header from './Header';
import Table from './Table';
import Add from './Add';
import Edit from './Edit';



const Dashboard = ({ isAuthenticated, setIsAuthenticated, setShowLogin }) => {
  const { courtAbbreviation } = useParams();
  // Use route param directly as abbreviation
  const abbreviation = courtAbbreviation ? courtAbbreviation.toUpperCase() : '';

  const [courts, setCourts] = useState([]);
  const [selectedCourt, setselectedCourt] = useState(null);
  const [isAdding, setIsAdding] = useState(false);
  const [isEditing, setIsEditing] = useState(false);
  const [timers, setTimers] = useState({});
  const [searchQuery, setSearchQuery] = useState('');
  const [userLocation, setUserLocation] = useState(null);

  const fetchCourtsDataRef = useRef();
  const timerIdsRef = useRef({});
  const handleToggleRef = useRef();
  const handleExpireRef = useRef();

  // Calculate distance between two coordinates using geolib's getDistance (returns meters)
  const calculateDistance = useCallback((lat1, lon1, lat2, lon2) => {
    if (lat1 == null || lon1 == null || lat2 == null || lon2 == null) return null;
    // getDistance returns meters, convert to km
    return getDistance(
      { latitude: lat1, longitude: lon1 },
      { latitude: lat2, longitude: lon2 }
    ) / 1000;
  }, []);

  // Get user's location on mount
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const coords = {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          };
          setUserLocation(coords);
        },
        (error) => {
          console.warn('Location access denied or unavailable:', error.message);
        },
        {
          enableHighAccuracy: true,
          timeout: 100000,
          maximumAge: 0
        }
      );
    }
  }, []);

  const fetchCourtsData = useCallback(async () => {
    try {
      let url = joinApi('/api/tables');
      if (abbreviation) {
        url += `?abbreviation=${abbreviation}`;
      }
      const res = await fetch(url);
      const data = await res.json();

      let rows = data.tables || [];
      const courtsMap = {};
      rows.forEach(row => {
        const courtId = row.id || row.court_id || row.courtId;
        courtsMap[courtId] = {
          id: courtId,
          name: row.name,
          address: row.address,
          capacity: row.capacity,
          lights: row.lights,
          duration: row.duration,
          bounds: row.bounds,
          latitude: row.latitude,
          longitude: row.longitude,
          occupied: row.occupied || 0,
          court_symbols: row.court_symbols || [],
          time_actives: row.time_actives || [],
          today_active: row.today_active || [],
          abbreviation: row.abbreviation
        };
      });
      let courtsData = Object.values(courtsMap);
      setCourts(courtsData); // Always set raw courts data
    } catch (error) {
      console.error('Failed to fetch courts data:', error);
      setCourts([]);
    }
  }, [abbreviation]);

  function processCourts(rawCourts, userLocation, searchQuery, calculateDistance, abbreviation) {
    let courtsData = rawCourts.map(court => {
      // Calculate userDistance if userLocation is available
      let userDistance = null;
      let hasValidCoordinates = false;
      if (userLocation && court.latitude != null && court.longitude != null) {
        userDistance = calculateDistance(
          userLocation.latitude,
          userLocation.longitude,
          court.latitude,
          court.longitude
        );
        hasValidCoordinates = userDistance != null;
      }
      // Keep userDistance as the backend value, add userDistance as a new property
      return { ...court, userDistance, hasValidCoordinates };
    });

    // Filter by abbreviation (route param)
    if (abbreviation) {
      courtsData = courtsData.filter(court => {
        // Match abbreviation to court.abbreviation property (exact match, case-insensitive)
        return (court.abbreviation || '').toUpperCase() === abbreviation;
      });
    }

    // Filter by search query
    const q = (searchQuery || '').trim().toLowerCase();
    if (q) {
      courtsData = courtsData.filter(court => {
        const name = (court.name || court.courtName || '').toString().toLowerCase();
        const address = (court.address || '').toString().toLowerCase();
        const capacity = court.capacity ? String(court.capacity) : '';
        return name.includes(q) || address.includes(q) || capacity.includes(q);
      });
    }

    // Sort by distance if user location is available
    if (userLocation) {
      courtsData = courtsData.slice().sort((a, b) => {
        if (a.hasValidCoordinates && !b.hasValidCoordinates) return -1;
        if (!a.hasValidCoordinates && b.hasValidCoordinates) return 1;
        if (!a.hasValidCoordinates && !b.hasValidCoordinates) return 0;
        return a.distance - b.distance;
      });
    }

    return courtsData;
  }

  const handleExpireTimer = useCallback(async id => {
    try {
      await fetch(joinApi(`/api/tables/${id}/expire-timer`), {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      // Clear the scheduled timeout (if any)
      try {
        if (timerIdsRef.current[id]) {
          clearTimeout(timerIdsRef.current[id]);
          delete timerIdsRef.current[id];
        }
      } catch (e) {
        // ignore
      }

      // Remove timer from state and localStorage using functional update
      setTimers(prev => {
        const newTimers = { ...prev };
        delete newTimers[id];
        localStorage.setItem('courtTimers', JSON.stringify(newTimers));
        return newTimers;
      });

      // Refetch courts data using the ref
      if (fetchCourtsDataRef.current) {
        await fetchCourtsDataRef.current();
      }

      // Find the court that contains this id
      const court = courts.find(c => 
        c.id === id || 
        (c.court_symbols && c.court_symbols.some(symbol => symbol.id === id))
      );
      
      if (!court) return;
      
    } catch (error) {
      console.error('Failed to expire timer:', error);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'Failed to update court occupancy.',
        showConfirmButton: false,
        timer: 5000,
      });
    }
  }, [courts]);

  // Keep a stable ref to the latest handleExpireTimer
  useEffect(() => {
    handleExpireRef.current = handleExpireTimer;
  }, [handleExpireTimer]);

  // Load active timers from localStorage on component mount
  useEffect(() => {
    const savedTimers = JSON.parse(localStorage.getItem('courtTimers') || '{}');
    const now = Date.now();
    // Filter out expired timers
    const activeTimers = {};
    Object.entries(savedTimers).forEach(([courtId, expireTime]) => {
      if (expireTime > now) {
        activeTimers[courtId] = expireTime;
        // Set timeout for remaining time
        const remainingTime = expireTime - now;
        const timeoutId = setTimeout(() => {
          if (handleExpireRef.current) handleExpireRef.current(parseInt(courtId));
        }, remainingTime);
        // store the timeout id in a ref so we can clear it later
        timerIdsRef.current[courtId] = timeoutId;
      }
    });
    setTimers(activeTimers);
    localStorage.setItem('courtTimers', JSON.stringify(activeTimers));

    // Cleanup on unmount: clear any scheduled timeouts
    return () => {
      try {
        Object.values(timerIdsRef.current).forEach(timerId => clearTimeout(timerId));
      } catch (e) {}
      timerIdsRef.current = {};
    };
  }, []);

  // Initial data fetch
  useEffect(() => {
    fetchCourtsData();
  }, [fetchCourtsData]);

  // Filter and sort courts for display
  const filteredCourts = useMemo(() => {
    const filtered = processCourts(courts, userLocation, searchQuery, calculateDistance, abbreviation);
    return filtered;
  }, [courts, searchQuery, userLocation, abbreviation, calculateDistance]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      // Clear any scheduled timeouts on unmount
      try {
        Object.values(timerIdsRef.current).forEach(timerId => clearTimeout(timerId));
      } catch (e) {}
      timerIdsRef.current = {};
    };
  }, []);

  // Define the missing functions
  const handleEdit = id => {
    const [court] = courts.filter(court => court.id === id);
    setselectedCourt(court);
    setIsEditing(true);
  };

  const handleDelete = id => {
    Swal.fire({
      icon: 'warning',
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, cancel!',
      customClass: { cancelButton: 'cancel-button' },
      buttonsStyling: false,
    }).then(result => {
      if (result.value) {
        const [court] = courts.filter(court => court.id === id);

        Swal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: `${court.courtName} ${court.address}'s data has been deleted.`,
          showConfirmButton: false,
          timer: 1500,
        });

        const courtsCopy = courts.filter(court => court.id !== id);
        localStorage.setItem('courts_data', JSON.stringify(courtsCopy));
        setCourts(courtsCopy);
      }
    });
  };

  const handleToggleOccupied = useCallback(async (id, event) => {
    const court = courts.find(court => court.id === id);
    if (!court) return;

    // Always show symbol selection popup, even if only one or zero symbols
    let symbols = Array.isArray(court.court_symbols) ? court.court_symbols : [];
    if (symbols.length === 0) {
      await doUpdate(court.id, '');
      return;
    }
    // If symbols is an array of strings, convert to objects for uniformity
    symbols = symbols.map((s, idx) => typeof s === 'string' ? { id: `${court.id}_${idx}`, symbol: s } : s);
    const html = symbols.map(symbol =>
      `<button class="swal2-confirm swal2-styled" style="margin: 6px 8px 0 0; font-size: 1.1em; padding: 8px 18px;" data-court-id=${symbol.id}>${symbol.symbol}</button>`
    ).join('');
    Swal.fire({
      icon: 'question',
      title: `Which court are you playing at?`,
      html,
      showConfirmButton: false,
      didOpen: () => {
        symbols.forEach(symbol => {
          const btn = Swal.getHtmlContainer().querySelector(`button[data-court-id="${symbol.id}"]`);
          if (btn) {
            btn.addEventListener('click', async () => {
              Swal.close();
              await doUpdate(court.id, symbol.symbol);
            });
          }
        });
      }
    });

    // Helper to handle the actual update for a specific court type
    async function doUpdate(courtTypeId, courtTypeLabel) {
      try {
        // Increment occupied value via backend for the selected court type
        const response = await fetch(joinApi(`/api/tables/${courtTypeId}/occupied`), {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ court_symbol: courtTypeLabel })
        });

        if (!response.ok) {
          throw new Error('Failed to update occupancy');
        }

        // Set timer for this court type (10 seconds for testing)
		let allowedUsageTime = court.duration;
        if (typeof allowedUsageTime === 'object' && allowedUsageTime !== null) {
          // Convert { hours, minutes, seconds } to ms
          allowedUsageTime =
            (allowedUsageTime.hours || 0) * 3600000 +
            (allowedUsageTime.minutes || 0) * 60000 +
            (allowedUsageTime.seconds || 0) * 1000;
        }

        const expireTime = Date.now() + allowedUsageTime;
        const newTimers = { ...timers, [courtTypeId]: expireTime };
        localStorage.setItem('courtTimers', JSON.stringify(newTimers));
        setTimers(newTimers);

        // Set timeout to automatically decrement after 10 seconds
        if (timerIdsRef.current[courtTypeId]) {
          clearTimeout(timerIdsRef.current[courtTypeId]);
        }
        const timeoutId = setTimeout(() => {
          handleExpireTimer(courtTypeId);
        }, allowedUsageTime);
        timerIdsRef.current[courtTypeId] = timeoutId;

        // Fallback: Manually refetch if WebSocket doesn't update within 1 second
        setTimeout(async () => {
          if (fetchCourtsDataRef.current) {
            await fetchCourtsDataRef.current();
          }
        }, 1000);

        Swal.fire({
          icon: 'success',
          title: 'Enjoy!',
          text: `Limit TOTAL playing time to ${allowedUsageTime / 60000} minutes when others are waiting.`,
          showConfirmButton: false,
          timer: 5000,
        });
      } catch (err) {
        console.error('Error updating occupancy:', err);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'Failed to update occupancy. Please try again.',
          showConfirmButton: true,
        });
      }
    }
  }, [courts, timers, handleExpireTimer]);

  // Keep a ref to the latest handleToggleOccupied so other callbacks can call it
  useEffect(() => {
    handleToggleRef.current = handleToggleOccupied;
  }, [handleToggleOccupied]);

  // Setup WebSocket connection for listening
  useSocket(joinApi(), (data) => {
    // Set raw courts data from socket
    setCourts(data.tables || []);
  });

  return (
    <div className="container">
      {!isAdding && !isEditing && (
        <>
          <Header
            isAuthenticated={isAuthenticated}
            setIsAdding={setIsAdding}
            setIsAuthenticated={setIsAuthenticated}
            setShowLogin={setShowLogin}
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            courtAbbreviation={courtAbbreviation}
          />
          <Table
            courts={filteredCourts}
            isAuthenticated={isAuthenticated}
            handleEdit={handleEdit}
            handleDelete={handleDelete}
            handleToggleOccupied={handleToggleOccupied}
            timers={timers}
          />
        </>
      )}
      {isAdding && (
        <Add
          courts={courts}
          setCourts={setCourts}
          setIsAdding={setIsAdding}
        />
      )}
      {isEditing && (
        <Edit
          courts={courts}
          selectedCourt={selectedCourt}
          setCourts={setCourts}
          setIsEditing={setIsEditing}
        />
      )}
    </div>
  );
};

export default Dashboard;