import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';


import Login from '../Login';
import Dashboard from '../Dashboard';
import Home from '../Home';

const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [showLogin, setShowLogin] = useState(false);

  useEffect(() => {
    const authStatus = JSON.parse(localStorage.getItem('is_authenticated'));
    setIsAuthenticated(authStatus || false);
  }, []);

  if (showLogin && !isAuthenticated) {
    return <Login setIsAuthenticated={setIsAuthenticated} setShowLogin={setShowLogin} />;
  }

  return (
    <Router>
      <Routes>
        {/* Courts with abbreviation */}
        <Route path="/:courtAbbreviation" element={
          <Dashboard 
            isAuthenticated={isAuthenticated}
            setIsAuthenticated={setIsAuthenticated}
            setShowLogin={setShowLogin}
          />} /> 
          {/* Dashboard Components needed for access to admin routes */}

        {/* Default route for everything else */}
        <Route path="/" element={<Home />} />
      </Routes>
    </Router>
  );
};

export default App;