import React, { useState, useEffect } from 'react';

const Table = ({ courts, isAuthenticated, handleEdit, handleDelete, handleToggleOccupied }) => {
  const [expanded, setExpanded] = useState({});
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      const isSmallScreen = window.innerWidth <= 768;
      setIsMobile(isSmallScreen);
    };
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const toggleExpand = (e, id) => {
  const tag = e.target && e.target.tagName ? e.target.tagName.toLowerCase() : '';
  if (['button', 'a', 'input'].includes(tag)) return;
  setExpanded(prev => ({ ...prev, [id]: !prev[id] }));
};

  return (
    <div className="contain-table">
      <table className="striped-table">
        <thead>
          <tr>
            <th></th>
            <th>Court Name</th>
            {isMobile && <th className="text-center">Action</th>}
            <th className="text-center">In Use</th>
            {!isMobile && <th>Current Usage</th>}
            {isAuthenticated && (
              <th colSpan={2} className="text-center">
                Management
              </th>
            )}
          </tr>
        </thead>
        <tbody>
          {courts.length > 0 ? (
            courts.map((court, i) => (
              <>
                <tr key={court.id} onClick={(e) => toggleExpand(e, court.id)} style={{ cursor: isMobile ? 'default' : 'pointer' }}>
                  <td>
                    {<span aria-hidden="true" style={{ fontSize: '1em' }}>{expanded[court.id] ? '' : ''}</span>}
                  </td>
                  <td>{court.name}</td>
                {isMobile && (<td>
                  {(() => {
                    // Only show button on mobile devices
                    // Check if user is within range
                    if (court.userDistance !== undefined && court.userDistance > court.bounds) {
                      return <div style={{ minHeight: '38px' }}></div>;
                    }
                    // Hide all 'I'm here' buttons if any timer is active
                    let timers = {};
                    try {
                      timers = JSON.parse(localStorage.getItem('courtTimers') || '{}');
                    } catch {}
                    const anyActive = Object.values(timers).some(expireTime => expireTime > Date.now());
                    const expireTime = timers[court.id];
                    if (anyActive) {
                      return <div style={{ minHeight: '38px' }}></div>;
                    }
                    if (!expireTime || Date.now() > expireTime) {
                      return (
                        <button
                          onClick={(e) => { e.stopPropagation(); handleToggleOccupied(court.id, e); }}
                          className="button"
							style={{
							  fontSize: '15px',
							  padding: '6px 10px',
							  minWidth: '80px',
							  whiteSpace: 'nowrap',
							  minHeight: '38px',
							  cursor: 'not-allowed',
							  border: '2px solid rgb(100, 100, 100)',
							  borderRadius: '6px'
							}}						  
                          
                        >
                          Click to Start
                        </button>
                      );
                    } else {
                      return <div style={{ minHeight: '38px' }}></div>;
                    }
                  })()}
                </td>)}
                <td className="text-center">
                  <span style={{
                    display: 'inline-block',
                    padding: '5px 10px',
                    border: '1px solid #ccc',
                    borderRadius: '6px',
                    background: court.occupied >= court.capacity ? '#db6262b9' : '#91f569b7',
                    fontWeight: 'bold',
                    minWidth: '40px',
                    textAlign: 'center'
                  }}>{`${court.occupied}/${court.capacity}`}</span>
                </td>
                <td>
                  {!isMobile
                    ? (court.time_actives && court.time_actives.length > 0
                      ? court.time_actives.slice(0, court.capacity)
                              .map((entry, idx) => (
                                <span key={idx}>
                                  {new Date(entry.pressed_at).toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit' })}
                                  {entry.court_symbol ? ` on court ${entry.court_symbol}` : ''}
                                  <br />
                                </span>
                              ))
                      : '')
                  : null}
                </td>
                {isAuthenticated && (
                  <>
                    <td className="text-right">
                      <button
                        onClick={(e) => { e.stopPropagation(); handleEdit(court.id); }}
                        className="button cancel-button"
                      >
                        Edit
                      </button>
                    </td>
                    <td className="text-left">
                      <button
                        onClick={(e) => { e.stopPropagation(); handleDelete(court.id); }}
                        className="button cancel-button"
                      >
                        Delete
                      </button>
                    </td>
                  </>
                )}
              </tr>

              {/* Expanded details row Desktop*/}
              {!isMobile && expanded[court.id] && (
                <tr key={`expanded-${court.id}`} style={{ background: '#f7faf7' }}>
                  <td colSpan={8} style={{ padding: 12 }}>
                    <div style={{ display: 'flex', gap: 20, flexWrap: 'wrap', alignItems: 'flex-start' }}>
                      <div style={{ minWidth: 220, maxWidth: 420 }}>
                        <strong>Recent Usage</strong>
                        <div>
                          {court.today_active && court.today_active.length > 0
                            ? court.today_active.map((entry, idx) => (
                                <div key={idx}>
                                  {new Date(entry.pressed_at).toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit' })}
                                  {entry.court_symbol ? ` on court ${entry.court_symbol}` : ''}
                                </div>
                              ))
                            : <div></div>
                          }
                        </div>
                      </div>
                      {/* <div style={{ minWidth: 160, maxWidth: 320 }}>
                        <strong>Address</strong>
                        <div>{court.address}</div>
                      </div> */}
                      <div style={{ minWidth: 120, maxWidth: 220 }}>
                        <strong>Total Courts</strong>
                        <div>{court.capacity}</div>
                      </div>
                      {/* <div style={{ minWidth: 120, maxWidth: 220 }}>
                        <strong>Night Lights</strong>
                        <div>{court.lights ? 'Yes' : 'No'}</div>
                      </div> */}
                    </div>
                  </td>
                </tr>
              )}
              {/* Expanded details row Mobile*/}
              {isMobile && expanded[court.id] && (
                <tr key={`expanded-${court.id}`} style={{ background: '#f7faf7' }}>
                  <td colSpan={8} style={{ padding: 12 }}>
                    <div
                      style={{
                        display: 'flex',
                        flexDirection: 'row',
                        flexWrap: 'nowrap',
                        gap: 12,
                        alignItems: 'flex-start',
                        overflowX: 'auto',
                      }}
                    >
                      <div style={{ minWidth: 140, maxWidth: 220, flex: 1 }}>
                        <strong>Current Usage</strong>
                        <div>
                          {court.time_actives && court.time_actives.length > 0
                            ? court.time_actives.slice(0, court.capacity)
                                .map((entry, idx) => (
                                  <span key={idx}>
                                    {new Date(entry.pressed_at).toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit' })}
                                    {entry.court_symbol ? ` on court ${entry.court_symbol}` : ''}
                                    <br />
                                  </span>
                                ))
                            : <div></div>
                          }
                        </div>
                      </div>
                      <div style={{ minWidth: 140, maxWidth: 220, flex: 1 }}>
                        <strong>Recent Usage</strong>
                        <div>
                          {court.today_active && court.today_active.length > 0 ? court.today_active.map((entry, idx) => (
                              <div key={idx}>
                                {new Date(entry.pressed_at).toLocaleTimeString([], { hour12: false, hour: '2-digit', minute: '2-digit' })}
                                {entry.court_symbol ? ` on court ${entry.court_symbol}` : ''}
                              </div>
                            ))
                          : <div></div>
                        }
                        </div>
                      </div>
                    </div>
                  </td>
                </tr>
              )}
            </>
            ))
          ) : (
            <tr>
              <td colSpan={4}>No courts</td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
