// client/src/apiBase.js
// Simple helper to centralize the API base URL and safe join logic.
// At build time set REACT_APP_API_URL (Create React App) or leave blank to use relative paths.
let API_BASE;

/**
 * joinApi('/api/tables') -> `${API_BASE}/api/tables` (handles slashes)
 * If API_BASE is empty, returns the original path for relative requests.
 */

if(window.location.hostname === 'localhost') {
  API_BASE = 'http://localhost:5000';
}else if (window.location.hostname === 'test.courtinuse.com'){
  API_BASE = "https://apitest.courtinuse.com";
} else if (window.location.hostname === 'www.courtinuse.com'){
  API_BASE = "https://api.courtinuse.com";
}


export function joinApi(path = '') {
  if (!path) return API_BASE || '';
  if (!API_BASE) return path;
  const base = API_BASE.replace(/\/+$/, '');        // remove trailing slashes
  const p = path.replace(/^\/+/, '');               // remove leading slashes
  return `${base}/${p}`;
}